import * as Ajv from 'ajv'
const ajv = Ajv()
export const editTags = ajv.compile({
  "type": "object",
  "properties": {
    "group": {
      "type": "number"
    },
    "post": {
      "type": "number"
    },
    "operation": {
      "type": "string",
      "enum": [
        "SET",
        "NEW",
        "DELETE"
      ]
    },
    "tags": {
      "type": "array",
      "items": {
        "type": "string"
      }
    }
  },
  "required": [
    "group",
    "post",
    "operation",
    "tags"
  ],
  "additionalProperties": false
})
export const deletePost = ajv.compile({
  "type": "object",
  "properties": {
    "group": {
      "type": "number"
    },
    "post": {
      "type": "number"
    }
  },
  "required": [
    "group",
    "post"
  ],
  "additionalProperties": false
})
export const editGroup = ajv.compile({
  "properties": {
    "name": {
      "type": "string"
    },
    "group": {
      "type": "number"
    },
    "users": {
      "type": "array",
      "items": {
        "type": "object",
        "properties": {
          "email": {
            "type": "string",
            "format": "email"
          },
          "permissions": {
            "type": "string",
            "enum": [
              "REMOVED",
              "READ",
              "WRITE",
              "ADMIN"
            ]
          }
        },
        "required": [
          "email",
          "permissions"
        ],
        "additionalProperties": false
      }
    }
  },
  "required": [
    "group"
  ],
  "minProperties": 2,
  "type": "object",
  "additionalProperties": false
})
export const getGroup = ajv.compile({
  "properties": {
    "id": {
      "type": "number"
    }
  },
  "type": "object",
  "required": [
    "id"
  ],
  "additionalProperties": false
})
export const getGroups = ajv.compile({
  "properties": {
    "page": {
      "type": "number"
    }
  },
  "type": "object",
  "required": [
    "page"
  ],
  "additionalProperties": false
})
export const getPosts = ajv.compile({
  "properties": {
    "group": {
      "type": "number"
    },
    "page": {
      "type": "number"
    },
    "tags": {
      "type": "array",
      "items": {
        "type": "string"
      }
    },
    "operation": {
      "type": "string",
      "enum": [
        "OR",
        "AND"
      ]
    }
  },
  "type": "object",
  "required": [
    "group",
    "page",
    "tags",
    "operation"
  ],
  "additionalProperties": false
})
export const getPost = ajv.compile({
  "properties": {
    "group": {
      "type": "number"
    },
    "id": {
      "type": "number"
    }
  },
  "type": "object",
  "required": [
    "group",
    "id"
  ],
  "additionalProperties": false
})
export const getPrefixes = ajv.compile({
  "properties": {
    "group": {
      "type": "number"
    },
    "prefix": {
      "type": "string"
    }
  },
  "type": "object",
  "required": [
    "group",
    "prefix"
  ],
  "additionalProperties": false
})
export const getPreview = ajv.compile({
  "type": "object",
  "properties": {
    "group": {
      "type": "number"
    },
    "post": {
      "type": "number"
    }
  },
  "required": [
    "group",
    "post"
  ],
  "additionalProperties": false
})
export const makePost = ajv.compile({
  "type": "object",
  "properties": {
    "group": {
      "type": "number"
    },
    "url": {
      "type": "string",
      "format": "uri",
      "pattern": "^(https?:|)//",
      "maxLength": 1024
    },
    "tags": {
      "type": "array",
      "items": {
        "type": "string"
      }
    },
    "mime": {
      "type": "string"
    }
  },
  "required": [
    "group",
    "url"
  ],
  "additionalProperties": false
})
export const makeGroup = ajv.compile({
  "properties": {
    "name": {
      "type": "string"
    }
  },
  "type": "object",
  "required": [
    "name"
  ],
  "additionalProperties": false
})
