import * as validators from './ApiValidators'
import { RPCRequest, RPCSuccess, RPCError, RPC } from 'jsonrpc-codegen/dist/RPC'
import * as resultTypes from './ApiResults'
import { Api } from './Api'

export async function apiHandler(handler: Api, msg: string): Promise<RPC> {
	let rpc: RPCRequest<any> = {} as unknown as any // just a temporary value
	// check parsing
	try {
		rpc = JSON.parse(msg)
	}
	catch(e) {
		return new RPCError(e, -32700)
	}
	// check conforms to JSON RPC standard
	if(rpc.jsonrpc != '2.0' || !rpc.method || !rpc.params) {
		return new RPCError('Not a JSON RPC', -32600)
	}

	// check and execute method
	const id = rpc.id
	switch(rpc.method) {
	case 'editTags':
		if(!validators.editTags(rpc.params)) {
			return new RPCError(JSON.stringify(validators.editTags.errors), -32602, id)
		}
		return new RPCSuccess<resultTypes.EditTagsResult>(await handler.editTags(rpc.params), id)
	case 'deletePost':
		if(!validators.deletePost(rpc.params)) {
			return new RPCError(JSON.stringify(validators.deletePost.errors), -32602, id)
		}
		return new RPCSuccess<resultTypes.DeletePostResult>(await handler.deletePost(rpc.params), id)
	case 'editGroup':
		if(!validators.editGroup(rpc.params)) {
			return new RPCError(JSON.stringify(validators.editGroup.errors), -32602, id)
		}
		return new RPCSuccess<resultTypes.EditGroupResult>(await handler.editGroup(rpc.params), id)
	case 'getGroup':
		if(!validators.getGroup(rpc.params)) {
			return new RPCError(JSON.stringify(validators.getGroup.errors), -32602, id)
		}
		return new RPCSuccess<resultTypes.GetGroupResult>(await handler.getGroup(rpc.params), id)
	case 'getGroups':
		if(!validators.getGroups(rpc.params)) {
			return new RPCError(JSON.stringify(validators.getGroups.errors), -32602, id)
		}
		return new RPCSuccess<resultTypes.GetGroupsResult>(await handler.getGroups(rpc.params), id)
	case 'getPosts':
		if(!validators.getPosts(rpc.params)) {
			return new RPCError(JSON.stringify(validators.getPosts.errors), -32602, id)
		}
		return new RPCSuccess<resultTypes.GetPostsResult>(await handler.getPosts(rpc.params), id)
	case 'getPost':
		if(!validators.getPost(rpc.params)) {
			return new RPCError(JSON.stringify(validators.getPost.errors), -32602, id)
		}
		return new RPCSuccess<resultTypes.GetPostResult>(await handler.getPost(rpc.params), id)
	case 'getPrefixes':
		if(!validators.getPrefixes(rpc.params)) {
			return new RPCError(JSON.stringify(validators.getPrefixes.errors), -32602, id)
		}
		return new RPCSuccess<resultTypes.GetPrefixesResult>(await handler.getPrefixes(rpc.params), id)
	case 'getPreview':
		if(!validators.getPreview(rpc.params)) {
			return new RPCError(JSON.stringify(validators.getPreview.errors), -32602, id)
		}
		return new RPCSuccess<resultTypes.GetPreviewResult>(await handler.getPreview(rpc.params), id)
	case 'makePost':
		if(!validators.makePost(rpc.params)) {
			return new RPCError(JSON.stringify(validators.makePost.errors), -32602, id)
		}
		return new RPCSuccess<resultTypes.MakePostResult>(await handler.makePost(rpc.params), id)
	case 'makeGroup':
		if(!validators.makeGroup(rpc.params)) {
			return new RPCError(JSON.stringify(validators.makeGroup.errors), -32602, id)
		}
		return new RPCSuccess<resultTypes.MakeGroupResult>(await handler.makeGroup(rpc.params), id)
	default:
		return new RPCError('Method does not exist', -32601)
	}
}
export const apiHandlerHOF = (handler: Api) => (msg: string) => apiHandler(handler, msg)