import { Api } from "./apiDefinitions/Api/Api";
import { RPCError, RPCSuccess, RPCResponse } from "jsonrpc-codegen/dist/RPC";
import * as results from './apiDefinitions/Api/ApiResults'

export type MethodNames = keyof Api
export type SendRPCParam<K extends MethodNames> = Parameters<Api[K]>[0]
type ThenArg<T> = T extends Promise<infer U> ? U : T
export type SendRPCType<K extends MethodNames> = Promise<ThenArg<ReturnType<Api[K]>>>

export const RESERVED_IDS = <const> [
	0,
	1,
]
export interface RESERVED_ID_EVENTS {
	0: results.AlertGroupMutateResult,
	1: results.AlertPostMutateResult,
}
export type ReservedIdEvents = typeof RESERVED_IDS[number]
export const STARTING_ID = RESERVED_IDS.length

export interface PositClient {
	sendRpc<K extends MethodNames>(methodName: K, params: SendRPCParam<K>, id?: number): SendRPCType<K>
	onError: (err: RPCError['error']) => void
	// enforces that RESERVED_IDS contains all keys in RESERVED_ID_EVENTS
	setListener<K extends ReservedIdEvents>(eventType: K, listener: (res: RESERVED_ID_EVENTS[K]) => void): void
}